@import url(https://fonts.googleapis.com/css2?family=Faster+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #EEF3F8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}

.algos {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.algo-menu {
  background: #fff;
  padding: 12px 20px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.algo-menu h4 {
  margin-right: 20px;
}
.algo-optioins {
  display: flex;
  flex-wrap: wrap;
}
.algo-visualize {
  flex: 1 1;
  background: #fff;
  padding: 10px 20px 40px 20px;
  border-radius: 8px;
  margin-top: 10px;
  display: flex;
  overflow: auto;
  flex-direction: column;
  justify-content: center;
}
.element-container {
  display: flex;
  flex: 1 1;
  border-bottom: 4px solid rgba(0,0,0,0.6);
  justify-content: center;
  align-items: flex-end;
}
.bars {
  background: #89C4F4;
  max-width: 20px;
  margin-right: 4px;
  text-align: center;
  color: rgba(0,0,0,0.6);
  font-size: 12px;
  /* transition: 0.3s; */
}

.disabled {
  pointer-events: none;
  opacity: 0.5;
  background: grey;
  border-radius: 4px;
}
/* .disabled > div {
  border-color: grey !important;
} */
button { 
  text-decoration: none;
  border: none;
  font-size: 16px;
  background-color:#1E8FD5;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.24);
  cursor: pointer;
  outline: none;
  transition: 0.2s all;
  width: 8em;
  height: 40px;
}
button:active { 
  transform: scale(0.98); 
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.24);
}
.position-being-sorted {
  background: #9b59b6;
}
.sorted {
  background: #2ecc71;
}
.min-element {
  background: #f1c40f;
}
.comparing-element {
  background: #e74c3c;
}
.unsorted {
  background: #89C4F4;
}

.color-indicators {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 5px;
}
.color-indicators > div {
  margin-right: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.color-indicators span {
  display: inline-block;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  border: 1px solid grey;
}

.algo-dropdown,
.elements-dropdown {
  width: 12em;
  margin-right: 10px;
}
