.definition {
  padding: 20px;
  background: #daedf8;
  border-radius: 8px;
  line-height: 1.7rem;
  font-size: 1.1rem;
  color: #232323;
  color: rgba(0,0,0,0.7)
}
.definition .definition-source {
  margin-top: 20px;
  font-weight: bold;;
  font-style: italic;
}

.instructions {
  text-align: center;
  font-size: 1.1rem;
  width: 100%;
  position: absolute;
  top: 150px;
  right: 50px;
  width: 300px;
  background: #daedf8;
  color: rgba(0,0,0,0.7);
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  padding: 10px 20px;
  border-radius: 4px;
}
