.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.main-section {
    flex: 1 1;
    margin: 10px 20px;
    display: flex;
}
header {
    background: #fff;
    min-height: 76px;
    display: flex;
    align-items: center;
    padding: 0 30px;
    justify-content: space-between;
}

header h1 {
    font-family: 'Faster One', cursive;
    font-size: 2.4rem;
}

header h1 span {
    color: #1E8FD5;
}
header h1 a {
    text-decoration: none;
    color: rgba(0,0,0,0.7)
}

nav ul {
    list-style-type: none;
    display: flex;
}
nav ul li {
    padding: 0 10px;
}

nav ul li a {
    padding: 5px 10px;
    text-decoration: none;
    color: #000;
    color: rgba(0,0,0,0.7);
    transition: 0.3s;
}
nav ul li a:hover, .active-nav-link {
    border-bottom: 1px solid #1E8FD5;
    color: #1E8FD5;
}

.home {
    background: #fff;
    padding: 10px 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.home-definition, .home-animation {
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
.home-definition {
    flex-direction: column;
    align-items: flex-start;
}

.footer {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: #fff;
    text-align: right;
    padding: 5px 40px;
    font-size: 0.9rem;
}

.footer span {
    font-style: italic;
    font-weight: bold;
}

.footer button {
    width: 40px;
    height: 40px;
    background: unset;
    margin-left: 20px;
    border-radius: 50%;
    border: none;
    box-shadow: unset;
    outline: 0;
}
.footer button:active {
    transform: scale(0.95);
}
